"use client"

import { useState } from "react"
import { AlertTriangle, AlertOctagon, Clock, ChevronRight, Check } from "lucide-react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"

const alerts = [
  {
    id: 1,
    type: "critical",
    medicine: "Paracetamol 500mg",
    facility: "City General Hospital",
    message: "Stock depleted - 0 units remaining",
    time: "5 min ago",
  },
  {
    id: 2,
    type: "critical",
    medicine: "Amoxicillin 250mg",
    facility: "District Pharmacy #12",
    message: "Critical shortage - 15 units left",
    time: "12 min ago",
  },
  {
    id: 3,
    type: "warning",
    medicine: "Metformin 500mg",
    facility: "Central Medical Store",
    message: "Low stock - 120 units remaining",
    time: "25 min ago",
  },
  {
    id: 4,
    type: "expiry",
    medicine: "Insulin Glargine",
    facility: "Memorial Hospital",
    message: "Batch expires in 7 days",
    time: "1 hour ago",
  },
  {
    id: 5,
    type: "warning",
    medicine: "Atorvastatin 10mg",
    facility: "Health Plus Pharmacy",
    message: "Below threshold - 85 units",
    time: "2 hours ago",
  },
]

const alertConfig = {
  critical: {
    icon: AlertOctagon,
    badgeVariant: "destructive" as const,
    label: "Critical",
  },
  warning: {
    icon: AlertTriangle,
    badgeVariant: "default" as const,
    label: "Warning",
  },
  expiry: {
    icon: Clock,
    badgeVariant: "secondary" as const,
    label: "Expiring",
  },
}

export function AlertsPanel() {
  const [acknowledgedAlerts, setAcknowledgedAlerts] = useState<Set<number>>(new Set())

  const handleAcknowledge = (alertId: number) => {
    setAcknowledgedAlerts(prev => new Set(prev).add(alertId))
  }

  return (
    <Card className="border-border bg-card">
      <CardHeader className="flex flex-row items-center justify-between">
        <div>
          <CardTitle className="text-lg text-card-foreground">Recent Alerts</CardTitle>
          <CardDescription>Critical shortages and warnings</CardDescription>
        </div>
        <Button variant="ghost" size="sm" className="gap-1 text-primary">
          View All
          <ChevronRight className="h-4 w-4" />
        </Button>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          {alerts.map((alert) => {
            const config = alertConfig[alert.type as keyof typeof alertConfig]
            const Icon = config.icon
            const isAcknowledged = acknowledgedAlerts.has(alert.id)
            const acknowledgedTime = isAcknowledged ? new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }) : null

            return (
              <div key={alert.id} className={`flex items-start gap-4 rounded-lg p-3 ${isAcknowledged ? 'bg-gray-100 border border-gray-200' : 'bg-muted/50'}`}>
                <div
                  className={`flex h-9 w-9 shrink-0 items-center justify-center rounded-full ${isAcknowledged ? 'bg-gray-200' : alert.type === "critical" ? "bg-destructive/20" : alert.type === "warning" ? "bg-warning/20" : "bg-muted"}`}
                >
                  <Icon
                    className={`h-4 w-4 ${isAcknowledged ? 'text-gray-500' : alert.type === "critical" ? "text-destructive" : alert.type === "warning" ? "text-warning" : "text-muted-foreground"}`}
                  />
                </div>
                <div className="min-w-0 flex-1">
                  <div className="flex items-center gap-2">
                    <span className={`font-medium ${isAcknowledged ? 'text-gray-600' : 'text-card-foreground'}`}>{alert.medicine}</span>
                    <Badge variant={isAcknowledged ? "secondary" : config.badgeVariant} className="text-xs">
                      {isAcknowledged ? "Acknowledged" : config.label}
                    </Badge>
                  </div>
                  <p className={`mt-0.5 text-sm ${isAcknowledged ? 'text-gray-500' : 'text-muted-foreground'}`}>{alert.facility}</p>
                  <p className={`mt-1 text-sm ${isAcknowledged ? 'text-gray-500' : 'text-muted-foreground'}`}>{alert.message}</p>
                  {isAcknowledged && acknowledgedTime && (
                    <p className="mt-1 text-xs text-gray-500">Acknowledged at {acknowledgedTime}</p>
                  )}
                </div>
                <div className="flex flex-col items-end gap-2">
                  {!isAcknowledged && (
                    <Button
                      size="sm"
                      variant="outline"
                      onClick={() => handleAcknowledge(alert.id)}
                      className="text-xs"
                    >
                      <Check className="h-3 w-3 mr-1" />
                      Acknowledge
                    </Button>
                  )}
                  <span className={`shrink-0 text-xs ${isAcknowledged ? 'text-gray-500' : 'text-muted-foreground'}`}>{alert.time}</span>
                </div>
              </div>
            )
          })}
        </div>
      </CardContent>
    </Card>
  )
}
