"use client"

import { useState, useEffect } from "react"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { Filter, Download, Info } from "lucide-react"
import {
  Tooltip,
  TooltipContent,
  TooltipProvider,
  TooltipTrigger,
} from "@/components/ui/tooltip"
import { useSearch } from "@/app/providers/search-context"

/* ================= TYPES ================= */

type StatusType = "adequate" | "low" | "critical"
type CategoryType = "OTC" | "Prescription"

interface InventoryItem {
  id: number
  name: string
  category: CategoryType
  quantity: number
  threshold: number
  status: StatusType
  facility: string
  expiryDate: string // ISO date string
}

/* ================= MOCK DATA ================= */

const inventoryData: InventoryItem[] = [
  {
    id: 1,
    name: "Paracetamol 500mg",
    category: "OTC",
    quantity: 2500,
    threshold: 500,
    status: "adequate",
    facility: "City General Hospital",
    expiryDate: "2025-12-31",
  },
  {
    id: 2,
    name: "Amoxicillin 250mg",
    category: "Prescription",
    quantity: 15,
    threshold: 100,
    status: "critical",
    facility: "District Pharmacy #12",
    expiryDate: "2024-02-15",
  },
]

/* ================= STATUS CONFIG ================= */

const statusConfig: Record<StatusType, string> = {
  adequate: "bg-green-100 text-green-800",
  low: "bg-yellow-100 text-yellow-800",
  critical: "bg-red-100 text-red-800",
}

/* ================= AI INSIGHTS (RULE BASED) ================= */

const getAIInsights = (item: InventoryItem): string[] => {
  const insights: string[] = []

  if (item.quantity === 0) {
    insights.push("Stock is completely depleted. Immediate redistribution required.")
  }

  if (item.quantity < item.threshold) {
    insights.push("Stock dropped rapidly below the minimum threshold.")
  }

  const expiryDate = new Date(item.expiryDate)
  const now = new Date()
  const daysUntilExpiry = Math.ceil((expiryDate.getTime() - now.getTime()) / (1000 * 60 * 60 * 24))

  if (daysUntilExpiry <= 30 && daysUntilExpiry > 0) {
    insights.push("Expiry approaching soon. Risk of wastage.")
  }

  if (item.status === "critical") {
    insights.push("High demand observed in recent activity.")
  }

  return insights
}

/* ================= COMPONENT ================= */

export function InventoryTable() {
  const { searchQuery } = useSearch()
  const [statusFilter, setStatusFilter] = useState("all")

  const filtered = inventoryData.filter(item => {
    const matchSearch = item.name.toLowerCase().includes(searchQuery.toLowerCase())
    const matchStatus =
      statusFilter === "all" || item.status === statusFilter
    return matchSearch && matchStatus
  })

  return (
    <Card>
      <CardHeader>
        <CardTitle>Inventory Overview</CardTitle>
        <CardDescription>Medicine stock monitoring</CardDescription>

        <div className="flex gap-3 mt-4">
          <Select value={statusFilter} onValueChange={setStatusFilter}>
            <SelectTrigger className="w-40">
              <SelectValue placeholder="Status" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">All</SelectItem>
              <SelectItem value="adequate">Adequate</SelectItem>
              <SelectItem value="low">Low</SelectItem>
              <SelectItem value="critical">Critical</SelectItem>
            </SelectContent>
          </Select>
        </div>
      </CardHeader>

      <CardContent>
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>Medicine</TableHead>
              <TableHead>Status</TableHead>
              <TableHead>Quantity</TableHead>
              <TableHead>Facility</TableHead>
              <TableHead>ðŸ¤– AI Insights</TableHead>
            </TableRow>
          </TableHeader>

          <TableBody>
            {filtered.length === 0 ? (
              <TableRow>
                <TableCell colSpan={5} className="text-center py-4">
                  No results found
                </TableCell>
              </TableRow>
            ) : (
              filtered.map(item => {
                const insights = getAIInsights(item)
                return (
                  <TableRow key={item.id}>
                    <TableCell>{item.name}</TableCell>

                    <TableCell>
                      <Badge className={statusConfig[item.status]}>
                        {item.status}
                      </Badge>
                    </TableCell>

                    <TableCell>{item.quantity}</TableCell>
                    <TableCell>{item.facility}</TableCell>

                    <TableCell>
                      <TooltipProvider>
                        <Tooltip>
                          <TooltipTrigger asChild>
                            <Button variant="ghost" size="sm">
                              <Info className="h-4 w-4" />
                            </Button>
                          </TooltipTrigger>
                          <TooltipContent>
                            <div className="max-w-xs">
                              <p className="font-semibold mb-2">ðŸ¤– AI-generated insights</p>
                              {insights.length > 0 ? (
                                <ul className="list-disc list-inside space-y-1">
                                  {insights.map((insight: string, index: number) => (
                                    <li key={index} className="text-sm">{insight}</li>
                                  ))}
                                </ul>
                              ) : (
                                <p className="text-sm">No critical risks detected.</p>
                              )}
                            </div>
                          </TooltipContent>
                        </Tooltip>
                      </TooltipProvider>
                    </TableCell>
                  </TableRow>
                )
              })
            )}
          </TableBody>
        </Table>
      </CardContent>
    </Card>
  )
}
